<template>
     <el-form  :model="searchForm" ref="searchForm"  label-width="110px" >
         <el-row>
             <el-col :span="6">
                 <el-form-item label="设备编号" prop="devCode">
                    <el-input v-model="searchForm.devCode" placeholder="请输入设备编号"></el-input>
                </el-form-item>
             </el-col>
             <el-col :span="6">
                 <el-form-item label="车牌号" prop="busPlateNumber">
                    <el-input v-model="searchForm.busPlateNumber" placeholder="请输入车牌号"></el-input>
                </el-form-item>
             </el-col>
             <el-col :span="6">
                 <el-form-item label="车辆自编号" prop="busSelfCode">
                    <el-input v-model="searchForm.busSelfCode" placeholder="请输入车辆自编号"></el-input>
                </el-form-item>
             </el-col>
             <el-col :span="6">
                 <el-form-item label="在线状态" prop="devOnlineStatus">
                    <el-radio-group v-model="searchForm.devOnlineStatus">
                        <el-radio :label="0">断开</el-radio>
                        <el-radio :label="1">在线</el-radio>
                    </el-radio-group>
                </el-form-item>
             </el-col>
         </el-row>
         <el-row>
            <!-- <el-col :span="8">
            <el-form-item label="起始时间" prop="notifyTime">
                <el-date-picker
                  style="width:100%"
                  v-entity="searchForm.notifyTime"
                  type="datetimerange"
                  :picker-options="dateBefore"
                  :default-time="[oftenTime, oftenTime]"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"/>
            </el-form-item>
           </el-col> -->
           <el-col :span="6" :offset="18" class="flex-right">
                <el-form-item>
                    <el-button type="primary" @click.native="search">查询</el-button>
                    <el-button @click="resetForm('searchForm')">重置</el-button>
                </el-form-item>
            </el-col>
         </el-row>
      </el-form>
</template>

<script>
import moment from 'moment'
export default {
  name: 'DeviceManagementSearch',
  data () {
    return {
      searchForm: {
        notifyTime: ['', ''],
        devCode: '', // 设备编号
        busPlateNumber: '', // 车牌号
        busSelfCode: '', // 车辆自编号
        devOnlineStatus: '', // 设备在线状态0：断开1：在线
        // mdStartDate: '', // 状态更新开始时间
        // mdEndDate: '', // 状态更新结束时间
        orderBy: '', // 排序字段
        descOrAsc: '' // 排序方式

      }
      // dateBefore: {
      //   disabledDate: (time) => {
      //     // return moment(time).diff(moment(), 'd') > -1
      //     return time.getTime() > new Date().getTime()
      //   }
      // },
      // oftenTime: moment().format('HH:mm:ss')
    }
  },
  watch: {
    // 'searchForm.notifyTime': function (time) {
    //   if (time.length) {
    //     this.searchForm.mdStartDate = moment(time[0])
    //     this.searchForm.mdEndDate = moment(time[1])
    //   }
    // }
  },
  mounted () {
    // this.initTime()
    this.search()
  },
  methods: {
    initTime () {
      const startTime = moment().subtract(6, 'd')
      const endTime = moment().subtract(0, 'd')
      this.searchForm.notifyTime = [startTime, endTime]
      this.searchForm.mdStartDate = startTime
      this.searchForm.mdEndDate = endTime
      // this.searchForm.devOnlineStatus = 0
    },
    // 转换查询参数为后端需要的参数格式
    convertSearchParams () {
      const searchParams = {
        devCode: this.searchForm.devCode, // 设备编号
        busPlateNumber: this.searchForm.busPlateNumber, // 车牌号
        busSelfCode: this.searchForm.busSelfCode, // 车辆自编号
        devOnlineStatus: this.searchForm.devOnlineStatus // 设备在线状态0：断开1：在线
        // mdStartDate: this.searchForm.mdStartDate, // 状态更新开始时间
        // mdEndDate: this.searchForm.mdEndDate // 状态更新结束时间

      }
      return searchParams
    },
    // 检索
    search: function () {
      this.$emit('search', Object.assign({}, this.convertSearchParams()))
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.$emit('rest', Object.assign({}, this.convertSearchParams()))
    }
  }

}
</script>
<style lang="scss" scoped>
.el-form-item{
   margin-bottom:10px;
}
</style>



